import React,{Component} from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CForm,
    CFormGroup,
    CInput,
    CLabel,
  } from '@coreui/react'

import API from 'src/api';

import SunEditor, { } from 'suneditor-react';
import 'suneditor/dist/css/suneditor.min.css';

import { history } from 'src/history';

const editorConfig = [
  ['align','fontColor','fontSize'],
  ['bold', 'underline', 'italic', 'strike', 'subscript', 'superscript'],
  ['undo', 'redo'],['outdent', 'indent'],
];
 
class EditUnit extends Component {
    constructor(props) {
      super(props);            
      this.state = {
        _id:this.props.match.params.id,
        title:'',
        content:'',
        topicId:'',
        isLoading:false
      }  
      this.handleInputChange = this.handleInputChange.bind(this);
      this.submitButton = this.submitButton.bind(this);      
    }

    componentDidMount(){
      this.getData();
    }

    handleInputChange(event) {
      const target = event.target;
      const value = target.type === 'checkbox' ? target.checked : target.value;
      const name = target.name;
  
      this.setState({
        [name]: value
      });
    }

    handleEditorChange(content){
      this.setState({
        content:content,
      })
    }

    async submitButton(){      

      let body = {
        "title":this.state.title,
        "content":this.state.content,
        "topicId":this.state.topicId,
      };
      await API.put("/unit/"+this.props.match.params.id, body)
      .then((response)=>{
        console.log(response.code)  
      }).catch((error)=>{
        alert("Try Again");
      });   
    }

    async getData(){
      try {
        this.setState({
          isLoading : true
        });
        var response = await API.get("/unit/"+this.props.match.params.id);          
        this.setState({
          _id : response.data.data._id,
          title : response.data.data.title,
          content : response.data.data.content,
          topicId:response.data.data.topicId,
          isLoading :false,
        });
      } catch (error) {   
        alert("Try Again");     
      }
    }

    resetButton = ()=>{           
        this.setState((prevState) => ({
          title:'',
          content:'',          
        }),);        
    }
  
    render() {
      let {title,content,isLoading} = this.state;

      if(isLoading){
        return (
          <>
            <div>Loading...</div>          
          </>
        )
      }
      return (
        <>
        <CCol xs="12">
        <CCard>
            <CCardHeader>
              <span className="h4">
                Edit Unit
              </span>
              <div className="btn btn-outline-info float-right mx-2" onClick={()=>{history.goBack()}}>
                Back
              </div>
            </CCardHeader>
            <CCardBody>
              <CForm action="" method="post" encType="multipart/form-data" className="form-horizontal">                
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="title">Title</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput onChange={this.handleInputChange} id="title" name="title" value={title} placeholder="Title" />                    
                  </CCol>
                </CFormGroup>                
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="content">Content</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    {/* <CInput onChange={this.handleInputChange} type="text" id="subTitle" name="subTitle" placeholder="Enter Chapter SubTitle"/>                     */}
                    <SunEditor defaultValue={content} setOptions={{ buttonList: editorConfig }} height="70%" onChange={(content) => { this.handleEditorChange(content) }} />
                  </CCol>
                </CFormGroup>             
              </CForm>
            </CCardBody>
            <CCardFooter>
              <CButton type="submit" className="m-1" size="sm" color="primary" onClick={this.submitButton}>Update</CButton>
              <CButton type="reset" size="sm" className="m-1" color="danger" onClick={this.resetButton}>Reset</CButton>
            </CCardFooter>
          </CCard>
        </CCol>
    </>
      );
    }
}

export default EditUnit;